.slider {
  .frame {
    position: relative;
    font-size: 0;
    line-height: 0;
    overflow: hidden;
    white-space: nowrap; }
  .slides {
    display: inline-block;
    li {
      position: relative;
      display: inline-block;
      padding: 0 92px;
      img {
        display: block;
        width: 100%; }
      @include r(1024) {
        padding: 0 50px; }
      @include r(600) {
        padding: 0; } } } }
